<template>
	<view class="body">
		<view class="content">
			<!-- 选择器 -->
			<view class="tab">
				<u-tabs :list="list1" @click="changeTab" lineWidth="100rpx" lineHeight="6rpx" lineColor="#ffd84d"
					inactiveStyle="font-size:30rpx;" activeStyle="font-size:34rpx;font-weight:600;color:#000;"></u-tabs>
			</view>
			<!-- 订阅列表 -->
			<view class="list">
				<view class="tips" v-if="data.length<1">暂无收藏~</view>
				<view class="cell" v-for="(item,index) in data" :key="index" @click="toSubList(item)">
					<view class="title">
						<view class="text" v-if="current==0">{{item.name || "未知"}}</view>
						<view class="text" v-else>{{item.name || "未知"}}</view>
						<view class="cancelButton" @click.stop="cancelCellect(item)">取消订阅</view>
					</view>
				</view>
			</view>
		</view>
	</view>
</template>

<script>
	import store from '@/store/index.js'; //引入store
	import {
		coltypelist, chartcol
	} from '@/common/http.api.js';
	export default {
		data() {
			return{
				token:'',
				pages:1,
				allPage:'',
				current:0,
				isLastPage:false,
				type:'school',
				data:[], //数据
				list1: [{
					name: '我订阅的学校',
				}, {
					name: '我订阅的专业',
				}],
			}
		},
		// 滑动到页面底部加载下一页
		onReachBottom() {
			// 判断是否有数据
				if(this.isLastPage==true){
					this.$u.toast("暂无更多")
				}else{
					this.pages++
					console.log(this.pages)
					let params = {
						token:this.token, 
						type:this.type,
						page:this.pages,
						totalNum:20,
					}
					coltypelist({...params}).then(res=>{
						console.log(res)
						if(res.data.code==-1){
							this.$u.toast(res.data.data)
						}
						res.data.data.length<20 ? this.isLastPage = true : ''
						this.data.push(...res.data.data)
						this.allPage = res.data.data.total
					}).catch(err=>{
						console.log(err)
					})
				}
				// this.getData(); // 获取数据
				console.log('我滚动到底部了')
		},
		onLoad() {
			
		},
		onShow() {
			this.token = store.state.vuex_token
			this.pages = 1
			this.current == 0 ? this.type='school' : this.type='major'
			let params = {
				token:this.token, 
				type:this.type,
				page:this.pages,
				totalNum:20,
			}
			coltypelist({...params}).then(res=>{
				console.log(res)
				if(res.data.code==-1){
					this.$u.toast(res.data.data)
				}
				res.data.data.length<20 ? this.isLastPage = true : ''
				this.data = res.data.data
				this.allPage = res.data.data.total
			}).catch(err=>{
				console.log(err)
			})
		},
		methods: {
			changeTab(item) {
				console.log('item', item);
				item.index == 0 ? this.type='school' : this.type='major'
				this.current = item.index
				let params = {
					token:this.token, 
					type:this.type,
					page:this.pages,
					totalNum:20,
				}
				coltypelist({...params}).then(res=>{
					console.log(res)
					if(res.data.code==-1){
						this.$u.toast(res.data.data)
					}
					res.data.data.length<20 ? this.isLastPage = true : ''
					this.data = res.data.data
					this.allPage = res.data.data.total
				}).catch(err=>{
					console.log(err)
				})
			},
			toSubList(item){
				if(this.current==0){
					uni.navigateTo({
						url:'/pages/index/subscribeList?id='+item.school_id+'&current='+this.current
					})
				}else{
					uni.navigateTo({
						url:'/pages/index/subscribeList?id='+item.major_id+'&current='+this.current
					})
				}
			},
			// 取消订阅
			cancelCellect(item){
				let id = item.school_id
				this.current == 0 ? id=item.school_id : id=item.major_id
				chartcol({token:this.token, id:id, state:0, type:this.type}).then(res=>{
					console.log(res)
					if(res.data.code==-1){
						this.$u.toast(res.data.data)
					}else{
						this.$u.toast('取消订阅成功')
						let params = {
							token:this.token, 
							type:this.type,
							page:this.pages,
							totalNum:20,
						}
						coltypelist({...params}).then(res=>{
							console.log(res)
							if(res.data.code==-1){
								this.$u.toast(res.data.data)
							}
							res.data.data.length<20 ? this.isLastPage = true : ''
							this.data = res.data.data
							this.allPage = res.data.data.total
						}).catch(err=>{
							console.log(err)
						})
					}
				}).catch(err=>{
					console.log(err)
				})
			},
		}
	}
</script>

<style lang="scss">
	.body{
		width: 100vw;
		padding: 1vh 0;
		min-height: 98vh;
		background-color: #f7f6fb;
		.content{
			margin: 0 5%;
			border-radius: 20rpx;
			padding: 0 20rpx;
			background-color: #fff;
			.tab {
				margin: 30rpx 0;
				display: flex;
				justify-content: center;
				.u-tabs__wrapper__nav__item {
					padding: 0 70rpx;
				}
			}
			.list {
				.tips{
					text-align: center;
					font-size: 24rpx;
					padding: 20rpx;
					padding-top: 0;
				}
				.cell {
					position: relative;
					.title {
						font-size: 32rpx;
						margin: 0 30rpx;
						padding: 40rpx 0;
						padding-left: 70rpx;
						border-bottom: 2rpx solid #f7f6fb;
						background-image: url(../../static/index/icon.png);
						background-repeat: no-repeat;
						background-position: 0rpx center;
						background-size: 40rpx;
						display: flex;
						justify-content: space-between;
						align-items: center;
						.text{
							width: 65%;
							text-overflow: ellipsis;
							white-space: nowrap;
							overflow: hidden;
						}
						.cancelButton{
							font-size: 24rpx;
							border-radius: 50rpx;
							background-color: #ee0000;
							padding: 4rpx 20rpx;
							color: #fff;
						}
					}
				}
			}
		}
	}
</style>